.tb_ug_m {
       position: fixed;
       top: 0;
       right: 0;
       bottom: 0;
       left: 0;
       overflow: hidden;
       outline: 0;
       display: none;
       align-items: center;
       justify-content: center;
       padding: 15px;
       z-index: 999999999;
       transition: all .300ms linear;
}

.tb_ug_m_w {
       max-width: 480px;
       margin: 2rem auto;
       width: auto;
       display: flex;
       flex-direction: column;
       width: 100%;
       pointer-events: auto;
       background-color: #ffffff;
       border-radius: 0.3rem;
       box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.10);
       padding: 30px;
       position: relative;
       transition: all 0.3s ease-out;
}

.tb_ug_m.tb_hide_ {
       display: none;
}

.tb_ug_m.tb_show_ {
       display: flex;
}

.tb_ug_m.tb_show_ .tb_ug_m_w {
       animation-duration: .3s;
       animation-name: tb_show_ani;
}

@keyframes tb_show_ani {
       from {
              opacity: 0;
              transform: scale(1.7);
       }

       to {
              opacity: 1;
              transform: scale(1);
       }
}

.tb_ug_body {
       display: flex;
       flex-direction: column;
       justify-content: center;
       position: relative;
}

.tb_ug_img {
       background-repeat: no-repeat;
       background-size: 75%;
       background-position: bottom center;
       padding-bottom: 36%;
       padding-top: 5%;
}

.tb_ug_img__{
       background-size: contain;
       margin-bottom: 24px;
       margin-top: 10px;
}

.tb_ug_close {
       position: absolute;
       right: 0;
       top: 0;
       opacity: .5;
       margin: 15px;
       padding: 5px;
       cursor: pointer;
       z-index: 1;
}

.tb_ug_close:hover {
       opacity: .8;
}

.tb_ug_title {
       font-size: 24px;
       margin-bottom: 15px;
       font-weight: 600;
       text-align: center;
}

.tb_ug_s_title {
       font-size: 14px;
       text-align: center;
       color: #545454;
       line-height: 1.6;
}

.tb_ug_s_title p{
       margin-top: 0;
}

.tb_ug_btn_w {
       margin: 20px auto;
       text-align: center;
}

.tb_ug_btn {
       background-color: #ff4051;
       border-radius: 3px;
       color: #fff;
       display: inline-block;
       font-size: 12px;
       font-weight: 400;
       line-height: 1.5;
       padding: 10px 15px;
       text-align: center;
       text-decoration: none;
       transition: color .3s ease-in-out, background-color .3s ease-in-out, border-color .3s ease-in-out, box-shadow .3s ease-in-out;
}

.tb_ug_btn:hover {
       background-color: #e23442;
       border-color: #e23442;
       color: #fff;
       text-decoration: none;
}

.tb_ug_overlay__ {
       background-color: rgba(0, 0, 0, 0.7);
       position: fixed;
       top: 0;
       left: 0;
       width: 100%;
       height: 100%;
       z-index: 999999998;
       display: none;
}

.tb_ug_overlay__.tb_show_ {
       display: block;
}